<template>
    <div>
        <div class="courses_header_wrap">
            <div class="courses_header">
                <img @click="back" src="data:image/png;base64," />
                <div>
                    <span v-show="!this.pageYOffset">课程详情</span>
                    <span v-show="this.pageYOffset" :class="this.pageYOffsetIndex == 0 ? 'sel' : 'selsel' " @click="page(0)">课程介绍</span>
                    <span v-show="this.pageYOffset" :class="this.pageYOffsetIndex == 1 ? 'sel' : 'selsel' " @click="page(1)">课程大纲</span>
                    <span v-show="this.pageYOffset" :class="this.pageYOffsetIndex == 2 ? 'sel' : 'selsel' " @click="page(2)">课程评价</span>
                </div>
                <img src="data:image/png;base64," />
            </div>
        </div>
        <div class="courses_title">
            <div class="courses_title_left">
                <div>{{title}}</div>
                <div style="color:orange">{{price | mf}}</div>
                <div style="color:lightgray">
                    <span>共{{total_periods}}课时</span>
                    |
                    <span>{{sales_num}}人已报名</span>
                </div>
            </div>
            <div class="courses_title_right">
                <span class="iconfont icon-shoucang1 collection" :style="this.iscollection ? 'color:orange' : 'gray' " @click="Collection"></span>
            </div>
        </div>

        <div class="courses_img">
            <div>教学团队</div>
            <div class="courses_img_item" v-for="(item,index) in teacher" :key="index">
                <img :src="item.teacher_avatar" />
                <div style="font-size:0.3rem;color:gray">{{item.teacher_name}}</div>
            </div>
        </div>

        <div class="courses_Introduction">
            <div ref="jieshao">课程介绍</div>
            <div style="font-size:0.37rem" v-html="this.course_details"></div>
        </div>

        <div class="courses_Introduction">
            <div ref="dagang">课程大纲</div>
            <div class="courses_Introduction_dian">
                <div style="font-size:0.8rem;margin-top:-0.3rem;color:orange">.</div>
                <div style="font-size:0.35rem" v-html="this.course_details"></div>
            </div>
        </div>

        <div class="courses_Comments">
            <div style="margin-top:0.5rem" ref="pinglun">课程评论</div>
            <div v-if="this.CommentsList.length == 0">
                <div class="img">
                    <img src="https://wap.365msmk.com/img/empty.0d284c2e.png" />
                    <div style="color:gray;font-size:0.35rem">暂无评论</div>
                </div>
            </div>
            <div v-else>
                <div v-for="(item,index) in CommentsList" :key="index" class="courses_Comments_item">
                    <div class="courses_Comments_item_img">
                        <img :src="item.avatar" />
                    </div>
                    <div class="courses_Comments_item_mobile">
                        <div class="courses_Comments_item_mobile_top">
                            <span>{{item.nickname}}</span>
                            <span style="color:#EB6100">
                                <span class="iconfont icon-xingxing"></span>
                                <span class="iconfont icon-xingxing"></span>
                                <span class="iconfont icon-xingxing"></span>
                                <span class="iconfont icon-xingxing"></span>
                                <span class="iconfont icon-xingxing"></span>
                            </span>
                            <span style="color:lightgray">{{item.created_at | time}}</span>
                        </div>
                        <div style="font-size:0.3rem;color:gray">
                            {{item.content}}
                        </div>
                        <div></div>
                    </div>
                </div>
            </div>
        </div>

        <div class="courses_footer"> 
            立即报名
        </div>

    </div>
</template>

<script>
export default {
    data(){
        return{
            title:'',
            price:0,
            total_periods:0,
            sales_num:0,
            teacher:[],
            course_details:'',
            CommentsList:[],
            iscollection:false,
            collect_id:0,
            pageYOffset:false,
            pageYOffsetIndex:0
        }
    },
    created(){
        document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
    mounted(){
        this.getCourses()
        this.Comments()
        window.addEventListener('scroll', this.handleScrollx, true)
    },
    methods:{
        async getCourses(){
            let { data } = await this.http.get(`/api/app/courseInfo/basis_id=${this.$route.query.id}`)
            this.title = data.data.info.title
            this.price = data.data.info.price
            this.total_periods = data.data.info.total_periods
            this.sales_num = data.data.info.sales_num
            this.teacher = data.data.teachers
            this.course_details = data.data.info.course_details
            window.console.log(data)
        },
        back(){
            window.history.back()
        },
        async Comments(){
            let {data:res} = await this.http.post('/api/app/courseComment',{id:this.$route.query.id,limit:10,page:1})
            window.console.log(res)
            this.CommentsList = res.data.list
        },
        Collection(){
            this.iscollection = !this.iscollection
            if(this.iscollection){
                this.http.post('/api/app/collect',{course_basis_id: this.$route.query.id,type: 1}).then((resp)=>{
                    window.console.log(resp)
                    if(resp.data.code == 200){
                        window.console.log(resp)
                        alert('收藏成功')
                        this.collect_id = resp.data.data
                    }
                })
            }else{
                this.http.put(`/api/app/collect/cancel/${this.collect_id}/1`).then((resp)=>{
                    window.console.log(resp)
                    if(resp.data.code == 200){
                        alert('取消收藏成功')
                    }
                })
            }
            
        },
        handleScrollx(e) {
        //   console.log('滚动高度', window.pageYOffset)
            this.pageYOffset = window.pageYOffset>10
        },
        page(sel){
            this.pageYOffsetIndex = sel
            if(sel == 0){
                this.$refs.jieshao.scrollIntoView({ behavior: "smooth" })
            }else if(sel == 1){
                this.$refs.dagang.scrollIntoView({ behavior: "smooth" })
            }else{
                this.$refs.pinglun.scrollIntoView({ behavior: "smooth" })
            }
        }
    },
    filters:{
        mf(value){
            if(value == 0){
                return "免费"
            }else{
                return (value/100).toFixed(2)
            }
        },
        time(value){
            let date = Number(value)
            var time = new Date(date).toLocaleString()
            return time
        }
    }
}
</script>

<style scoped>
.courses_header_wrap{
    width: 100%;
    height: 101px;
}
.courses_header{
    position: fixed;
    top: 0rem;
    width: 100%;
    height: 100px;
    border-bottom: 1px solid lightgray;
    font-size: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
}
.courses_header img{
    height: 30%;
    margin: 0rem 0.4rem;
}
.courses_title{
    width: 100%;
    height: 3.5rem;
    display: flex;
    margin-bottom: 0.5rem;
    background: white;
}
.courses_title_left{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    width: 85%;
    height: 100%;
    font-size: 0.45rem;
    /* background: red; */
    padding-left:5%;
}
.courses_title_right{
    width: 10%;
    height: 100%;
    /* background: yellow; */
}
.courses_img{
    width: 95%;
    height: 3.5rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    margin-bottom: 0.5rem;
    font-size: 0.4rem;
    background: white;
    padding-left: 5%;
}
.courses_img_item{
    width: 100%;
    height: 50%;
}
.courses_img_item img{
    width: 10%;
    height: 50%;
    border-radius: 50%;
}
.courses_Introduction{
    width: 90%;
    height: 3rem;
    background: white;
    margin-bottom: 0.5rem;
    font-size: 0.4rem;
    padding:0rem 5%;
    display: flex;
    justify-content: space-around;
    flex-direction:column;
}
.courses_Introduction_dian{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.courses_Comments{
    width: 90%;
    background: white;
    margin-bottom: 1.5rem;
    font-size: 0.4rem;
    padding:0rem 5%;
    display: flex;
    justify-content: space-around;
    flex-direction:column;
}
.courses_Comments_item{
    width: 100%;
    height: 1.7rem;
    display: flex;
    background: white;
}
.courses_Comments_item_img{
    width: 15%;
    height: 100%;
    display: flex;
    justify-content: center;
    /* background: red; */
}
.courses_Comments_item_img img{
    width: 50%;
    height: 40%;
    border-radius: 50%;
}
.courses_Comments_item_mobile{
    width: 85%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    /* background: yellow; */
}
.courses_Comments_item_mobile_top{
    width: 100%;
    font-size: 0.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.courses_footer{
    position: fixed;
    bottom: 0rem;
    width: 100%;
    height: 1.2rem;
    font-size: 0.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background: #EB6100;
}
.img{
    width: 100%;
    height: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.img img{
    height: 70%;
}
.collection{
    font-size: 0.7rem;
}
.sel{
    font-size:0.4rem;
    margin-left:0.2rem;
    color: black;
}
.selsel{
    font-size:0.4rem;
    margin-left:0.2rem;
    color:gray
}
</style>